
import './Terapias.css';

import Card from 'react-bootstrap/Card';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWind } from '@fortawesome/free-solid-svg-icons';
import { faEyeDropper } from '@fortawesome/free-solid-svg-icons';
import { faHandsHoldingCircle } from '@fortawesome/free-solid-svg-icons';
import { faWaveSquare } from '@fortawesome/free-solid-svg-icons';
import { faMoon } from '@fortawesome/free-solid-svg-icons';
import { fa7 } from '@fortawesome/free-solid-svg-icons';
import { Fade } from "react-awesome-reveal";
/* import { NavLink } from "react-router-dom"; */
import { HashLink } from 'react-router-hash-link';

export const Terapias = () => {
    return (
        <div className="container p-4 pt-5 pb-5">
            <Fade direction='left' cascade damping={0.1} triggerOnce={true}>
                <h1 className="title text-center pb-2 pt-5">Terapias y tratamientos</h1>
                <h2 className="text text-center pt-4 pb-4">Herramientas para que ayudarte a sanar conectando con tu poder interior y energa.</h2>
            </Fade>
            <div class="row row-cols-1 row-cols-lg-3 g-5 cards-container">

            <div class="col">
                    <Card className="card-elem text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={faWaveSquare} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Terapia con frecuencias</Card.Title>
                            <Card.Text className="text card-component">
                                Resonador holistico que emplea frecuencias de alta vibracin cuntica para ayudarte al equilibrio.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link as={HashLink} to="/Terapias/Frecuencias" className='text card-component'>Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>
                
                <div class="col">
                    <Card className="card-elem d-flex text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={faWind} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Aromaterapia</Card.Title>
                            <Card.Text className="text card-component">
                                Medicina Alternativa basada en el uso de distintos aceites esenciales de la naturaleza para el beneficio del cuerpo y de las emociones asociadas. En mi espacio uso los aceites Just.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link as={HashLink} to="/Terapias/Aromaterapia" className='text card-component'>Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>

                <div class="col">
                    <Card className="card-elem text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={faEyeDropper} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Flores de Bach y Mediterrneo</Card.Title>
                            <Card.Text className="text card-component">
                                Se utilizan esencias florales especficas para ayudar a generar los cambios emocionales y sentimentales que precises en tu vida.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link as={HashLink} to="/Terapias/Flores" className='text card-component'>Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>

                <div class="col">
                    <Card className="card-elem text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={faHandsHoldingCircle} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Barras de Access</Card.Title>
                            <Card.Text className="text card-component">
                                Tcnica de aplicacin manual que trabaja energticamente con el inconsciente para generar cambios psicolgicos y emocionales, eliminando los pensamientos limitantes.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link as={HashLink} to="/Terapias/Barras" className='text card-component'>Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>


                <div class="col">
                    <Card className="card-elem text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={faMoon} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Cartas natales</Card.Title>
                            <Card.Text className="text card-component">
                                Es una lectura del dibujo del cielo en la tierra en el momento de tu nacimiento, que permite saber como los astros te ayudan a trascender obstculos y lograr propsitos de vida.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link className='text card-component' as={HashLink} to="/Terapias/CartasNatales">Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>

                <div class="col">
                    <Card className="card-elem text-center h-100">
                        <Card.Body>
                            <FontAwesomeIcon icon={fa7} className="card-component icon-color" />
                            <Card.Title className="subtitle card-component">Cartas numerolgicas</Card.Title>
                            <Card.Text className="text card-component">
                                Descubr tu misin de vida y las tendencias que tendrs en tu futuro a travs de las vibraciones que crean tus nmeros.
                            </Card.Text>
                        </Card.Body>
                        <Card.Footer>
                            <Card.Link className='text card-component' as={HashLink} to="/Terapias/Numerologia">Leer ms</Card.Link>
                        </Card.Footer>
                    </Card>
                </div>
            </div>
        </div>
    )
}